main {
  height: 100%;
}

#project-board {
  /* overlap with ProjectBoard.css */
  display: flex;
  width: 100%;
  overflow-x: scroll;
}

.stage {
  min-width: 355px;
  max-width: 355px;
  flex-grow: 1;
  margin: 0.5rem;
  background-color: rgb(202, 202, 202);
  border-color: #aaa;
  border-radius: 0.5rem;
  overflow-y: auto;
  height: calc(100vh - 100px);
}

.stage-header {
  min-height: 16px;
  margin: 8px 8px 16px 8px;
}

.stage-title {
  float: left;
  margin: 0rem;
}

.btn {
  width: 100%;
  padding: 4px 16px;
  display: block;
  background-color: transparent;
  background-repeat: no-repeat;
  border: none;
  font-size: 1rem;
  text-align: left;
  cursor: pointer;
  overflow: hidden;
  outline: none;
}

.dropdown-content ul {
  list-style: none;
  padding: 0;
}

.dropdown-content li:hover,
.tabrow li:hover {
  background-color: #d1e99f;
}

.tabrow li.add-project {
  border: none;
  padding: 0;
  margin: 0;
  background: none;
}
.tabrow svg {
  width: 100%;
}

.btn-stage-delete {
  display: inline;
  text-align: center;
  background-color: transparent;
  background-repeat: no-repeat;
  border: none;
  padding: 0rem;
  font-size: 0.875rem;
  color: red;
  cursor: pointer;
  overflow: hidden;
  outline: none;
}
.stage-options {
  float: right;
}

.dropdown {
  /* float: right; */
  position: relative;
  display: inline-block;
  margin: 0 0.25rem 0 0.25rem;
}

.dropdown-content {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  background-color: #f9f9f9;
  min-width: 160px;
  padding: 4px;
  border: 1px solid #2f353c;
  border-radius: 0.5rem;
  box-shadow: -4px 8px 24px gray;
}

.dropdown:hover .dropdown-content {
  display: block;
  position: absolute;
}

.overlay {
  position: fixed;
  z-index: 99;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.33);
  display: flex;
  align-items: center;
  transition: opacity 0.3s;
}
.overlay-inner {
  position: absolute;
  width: 260px;
  top: 100px;
  left: 0;
  right: 0;
  margin: auto;
  padding: 8px;
  border-radius: 0.5rem;
  background-color: #f9f9f9;
  border: 1px solid #2f353c;
  border-radius: 0.5rem;
  box-shadow: -4px 8px 24px gray;
}

.overlay-inner label {
  font-weight: bold;
}

.overlay-inner input {
  width: 100%;
  margin-top: 4px;
}

.stage summary {
  list-style: none;
}
.stage.create {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  border: 1px dashed;
  overflow: hidden;
  background-color: #ececec;
}
.stage.create:hover {
  background-color: rgb(202, 202, 202);
}
.stage.create button {
  padding: 40px 0;
  font-weight: 600;
  text-align: center;
}

.btn-toggle-task-creator {
  float: right;
  position: relative;
  margin: 0 0.25rem 0 0.25rem;
}
